/* eslint-disable max-len */
/* eslint-disable import/no-dynamic-require */
import axios from '@/utils/axios';
import domain from '@/utils/document-domain';
import useLoader from '@4dst-saas/public-utils/dist/composables/use-loader';
import isQt from '@/utils/is-qt';
import {
  computed, defineComponent, reactive, ref, toRefs,
} from '@vue/composition-api';
// @ts-ignore
import { ICategoryTree } from '@/utils/get-material-category';


type MaterialMallBanner = {
  attachInfo?: any
  id: string | number
  image: string
};

function generateDefaultSlotByCategory(
  this: InstanceType<typeof TonContent>,
  category: number,
  h = this.$createElement,
) {
  switch (category) {
    case 0: return <div style={{
      fontSize: '46px',
      marginBottom: '22px',
      lineHeight: '65px',
      color: '#fff',
    }}>{this.$t('material-mall.explore_available_material')}</div>;
    default: return null;
  }
}

const TonContent = defineComponent({
  props: {
    categoryList: {
      type: Array as unknown as () => ICategoryTree[],
      required: true,
    },
    currentCategory: {
      type: Number,
      required: true,
    },
  },
  model: {
    prop: 'currentCategory',
    event: 'change',
  },
  render() {
    const t = (name: string) => this.$t(`material.material-category.${name}`);
    const te = (name: string) => `material.material-category.${name}`;
    return <div class='flex' style={{
      flex: 1,
      position: 'relative',
      height: 'auto',
    }}>
      <img src={this.currentImage?.image} style={{
        width: '100%',
        height: this.useDefaultHeight ? `${this.imageHeight}px` : undefined,
      }} onerror={() => { this.useDefaultHeight = true; }} />

      <div class="flex-center flex-column" style={{
        position: 'absolute',
        width: '100%',
        height: '100%',
      }}>
        {/* @ts-ignore */}
        {generateDefaultSlotByCategory.call(this, this.currentCategory)}
        {this.$scopedSlots.default ? this.$scopedSlots.default(null) : null}
      </div>

      <ul class='flex' style={{
        position: 'absolute',
        background: 'linear-gradient(to bottom, #000, transparent)',
        width: '100%',
        height: '50px',
        borderRadius: '8px 8px 0 0',
        top: 0,
      }}>
        {
          (this.handledCategoryList).map((category, i) => <el-dropdown
          oncommand={this.jump2SubCategory} class='asdlkfj'>
            <i18n tag='li' style={{
              margin: '6px 23px',
              padding: '6px 0',
              color: '#fff',
              cursor: i !== this.currentCategory ? 'pointer' : 'default',
              borderBottom: i === this.currentCategory ? '2px solid #2878ff' : undefined,
              outline: 'none',
            }} onclick={() => this.$emit('change', i)}
            options={{
              defaultValue: this.$t('material.others.home'),
            }}
            path={te(category.name)}></i18n>
            {
              category.sub ? <el-dropdown-menu slot='dropdown'>
                {category.sub.map(({ name, id }) => <el-dropdown-item class="dropdown-item flex-vertical-center"
                command={[{
                  name: category.name,
                  id: category.id,
                }, {
                  name,
                  id,
                }]}
                ><span style={{ fontSize: '20px', padding: '0 10px 0 0' }}>·</span>{t(name)}</el-dropdown-item>)}
              </el-dropdown-menu> : null
            }
          </el-dropdown>)
        }
      </ul>
    </div>;
  },
  setup(props, { root }) {
    const useDefaultHeight = ref(false);
    const imageHeight = ref(480);
    const currentImageIndex = ref(0);
    const bannerList = useLoader(async ({ currentCategory }) => {
      // return [
      //   {
      //     id: 0,
      //     image: 'http://dev.4dshoetech.local/datapipe/repository/binaryFile/64f783e969e5b2cc4a291b18c343d120.png?ticket=eyJjb3JwaWQiOiIxMTgzOTM0Njg2MDUzOTI4OTYxIiwiaWF0IjoxNjA3MzMzNzY3LCJubyI6IjEzMzU4ODA3NjA2MjAxNjcxNjkiLCJxcnN1bSI6ImQ0MWQ4Y2Q5OGYwMGIyMDRlOTgwMDk5OGVjZjg0MjdlIiwicmVzIjoiYmluYXJ5RmlsZS82NGY3ODNlOTY5ZTViMmNjNGEyOTFiMThjMzQzZDEyMC5wbmciLCJ1aWQiOiIxMTgzOTM0Njg1OTk1MjA4NzA1In0=.936705643a18b6266e6b7d187450652c5f03c2bc444fdf0ccafeb797402e53ac',
      //   },
      //   {
      //     id: 1,
      //     image: 'http://idoc.4dshoetech.local/idoc/2020-12-07/82d89920-3834-11eb-b90e-87d22ed6da83.7364645F-EE60-4614-ADCD-8D6D601BD600.png',
      //   },
      // ] as MaterialMallBanner[];
      const list = (await axios.get<MaterialMallBanner[]>('/mall/recommend/banner', {
        params: { categoryId: currentCategory },
      })).data;
      return list;
    }, reactive({ currentCategory: toRefs(props).currentCategory }), {
      initialValue: null,
      loadingValue: (pre) => pre ?? null,
    });
    const currentImage = computed(() => bannerList.value?.[currentImageIndex.value]);
    function jump2SubCategory(currentCategoryPaths: Partial<ICategoryTree>[]) {
      root.$router.push({
        path: `${isQt() ? '/qt' : ''}/material-mall/list`,
        query: {
          ...root.$route.query,
          searchParamsStr: JSON.stringify({
            currentCategoryPaths,
          }),
        },
      });
    }
    return {
      useDefaultHeight,
      imageHeight,
      bannerList,
      currentImage,
      handledCategoryList: computed(() => [{
        name: '首页',
        id: '0',
      } as ICategoryTree, ...props.categoryList]),
      jump2SubCategory,
    };
  },
});

export default TonContent;
